.button {
  font-size: 1.5rem;
  font-weight: 400;
  font-family: Julius Sans One, sans-serif;
  color: #333;
  position: relative;
  width: 13.75rem;
  padding: 3.125rem 3.125rem 3.125rem 6.25rem;
}
@media screen and (max-width: 767px) {
  .button {
    font-size: 1.125rem;
    width: 9.375rem;
    padding: 1.875rem 1.875rem 1.875rem 3.75rem;
  }
}

.button::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  border: 1px solid #333;
  border-radius: 50%;
  border-right-color: transparent;
  height: 100px;
  width: 100px;
  transition: 0.3s;
}
@media screen and (max-width: 767px) {
  .button::before {
    height: 60px;
    width: 60px;
  }
}

.button::after {
  content: "";
  position: absolute;
  top: 3rem;
  left: 2.3125rem;
  width: 11.25rem;
  height: 1.875rem;
  border: none;
  border-right: 1px solid #333;
  border-bottom: 1px solid #333;
  transform: skew(45deg);
  transition: 0.3s;
}
@media screen and (max-width: 767px) {
  .button::after {
    width: 7.5rem;
    height: 1.25rem;
    top: 1.9375rem;
    left: 1.25rem;
  }
}

.button:hover::after {
  width: 17.5rem;
}
@media screen and (max-width: 767px) {
  .button:hover::after {
    width: 15.625rem;
  }
}

