.hamburger {
  display: none;
}
@media screen and (max-width: 767px) {
  .hamburger {
    display: block;
    z-index: 9999;
  }
}

@media screen and (max-width: 767px) {
  .hamburger span {
    position: relative;
    display: block;
    height: 0.125rem;
    width: 1.375rem;
    background: #2C100F;
    border-radius: 0.0625rem;
    transition: transform 0.3s ease 0s;
  }
}

.hamburger span:nth-child(1) {
  top: 0;
}

.hamburger span:nth-child(2) {
  margin: 0.25rem 0;
}

.hamburger span:nth-child(3) {
  top: 0;
}

/*OPEN時の動き*/
.hamburger.open span:nth-child(1) {
  top: 5px;
  transform: rotate(45deg);
}

.hamburger.open span:nth-child(2) {
  transform: translateY(-50%);
  opacity: 0;
}

.hamburger.open span:nth-child(3) {
  top: -7px;
  transform: rotate(-45deg);
}

/*is-OPEN時の中身*/
@keyframes open {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.drawer-menu {
  display: none;
}
@media screen and (max-width: 767px) {
  .drawer-menu {
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    display: none;
    opacity: 0;
    transition: opacity 1s ease 0s;
  }
}

/*OPEN時の動き*/
.drawer-menu.open {
  display: block;
  opacity: 1;
  background-color: #fff;
  animation: open 1s ease 0s;
}

.drawer-menu__items {
  margin: 13.0625rem auto;
}

.drawer-menu__item {
  display: block;
  text-align: center;
}
.drawer-menu__item:not(:last-child) {
