}

.pc-nav__items {
  height: inherit;
  display: flex;
  align-items: center;
}

.pc-nav__item {
  height: inherit;
}

.pc-nav__item a {
  font-family: Julius Sans One, sans-serif;
  font-size: 1.5rem;
  letter-spacing: 0.05em;
  display: grid;
  place-items: center;
  padding: 0 1.075rem;
  position: relative;
  transition: all 0.3s ease 0s;
  color: #2C100F;
  height: inherit;
}

.pc-nav__item--contact a {
  font-family: Julius Sans One, sans-serif;
  font-size: 1.5rem;
  color: #fff;
  letter-spacing: 0.05em;
  padding: 0.5625rem 1.075rem;
  background-color: #2C100F;
  border-radius: 1.875rem;
  position: relative;
  place-items: center;
  transition: all 0.3s ease 0s;
}

.pc-nav__item a:hover::after {
  position: absolute;
  content: "";
  width: 80%;
  height: 1px;
  bottom: 8px;
  left: 50%;
  transform: translateX(-50%);
  box-sizing: content-box;
}

.pc-nav__item.pc-nav__item--white {
  margin-left: 1rem;
}

.pc-nav__item.pc-nav__item--white a {
  background-color: #fff;
  padding: 0 2rem;
  letter-spacing: 0.1em;
}

.pc-nav__item.pc-nav__item--white a:hover::after {
  display: none;
}

